<template>

    <div style="width: 100%;">

        <v-flex xs12 sm12>

            <v-card>

                <!--Cabecalho-->
                <v-card-title class="white--text blue">
                    <span class="headline">
                        Minha disponibilidade
                    </span>

                    <v-spacer></v-spacer>
                    <div v-if="isMyProfile" class="white" style="border-radius: 100px; padding: 2px">
                        <v-progress-circular
                                :size="circularSize"
                                :width="7"
                                :rotate="360"
                                title="Percentagem de preenchimento"
                                style="float: right;cursor: pointer"
                                :value="personalInfoFillPercent"
                                :color="hasSubjects ? 'green' : 'red'"
                                @click="toggle = !toggle"
                        >
                            <span v-if="hasSubjects && toggle">{{ personalInfoFillPercent }}</span>
                            <span v-else-if="hasSubjects" title="Disciplinas registadas!" ><v-icon>done</v-icon></span>
                            <span v-else="" title="Clique em editar, para registar as disciplinas que explicas">{{ personalInfoFillPercent }}</span>
                        </v-progress-circular>
                    </div>
                    <explicador-edit-subjects-dialog
                            style="float: right;"
                            v-if="isMyProfile"
                            :isMobile="true"
                            :showSubjectsEditDialog="showSubjectsEditDialog"
                            :user="user">
                    </explicador-edit-subjects-dialog>
                    <template v-else="">
                        <v-btn
                                :class="{'red--text' : !hasSubjects }"
                                fab=""
                                flat=""
                                small=""
                                outline=""
                                style="float: right;"
                        >
                            <v-icon class="white--text" :class="{'red--text' : !hasSubjects }">more_horiz</v-icon>
                        </v-btn>
                    </template>
                </v-card-title>

                <!--Corpo-->
                <v-list>

                    <!--Disponibilidade actual-->
                    <template>
                        <v-subheader>Disponibilidade actual - {{ available ? 'Disponível' : 'Indisponivel' }}</v-subheader>

                        <v-subheader class="hidden-xs-only mb-4">Voçê agora está <b class="ml-2" :class="available ? 'green--text' : 'red--text'">
                            {{ available ? 'Disponível' : 'Indisponível' }}</b>, Clique no lado direito para alterar
                        </v-subheader>

                        <v-layout :class="{'mb-4' : !available }">
                            <v-flex xs12 sm6>
                                <v-chip style="float: left" class="ml-3" :color="available ? 'green' : 'grey'" text-color="white">
                                    <v-avatar>
                                        <v-icon>check_circle</v-icon>
                                    </v-avatar>
                                    {{ available ? 'Disponível' : 'Indisponivel' }}
                                </v-chip>
                            </v-flex>
                            <v-flex xs12 sm6>
                                <v-switch :label="available ? yes : no " :color="available ? 'green' : 'grey'" v-model="available"></v-switch>
                            </v-flex>
                        </v-layout>
                        <v-layout>
                            <small style="float: left" class="ml-3 text-xs-left">
                                NOTA: Se desejar ser visualizado ao listarmos os explicadores, mantenha a sua disponibilidade actual como Sim, se mudar para não, ficarás oculto.
                            </small>
                        </v-layout>
                    </template>

                    <!--Dias de semana-->
                    <template v-if="available">

                        <!--Meio de semana-->
                        <template>
                            <v-subheader class="mt-4">Meio de semana</v-subheader>

                            <!--Segunda-feira-->
                            <v-layout>
                                <v-flex xs12 sm6>
                                    <v-chip style="float: left" class="ml-3" :color="monday ? availableColor : notAvailableColor" text-color="white">
                                        <v-avatar>
                                            <v-icon>check_circle</v-icon>
                                        </v-avatar>
                                        Segunda-feira
                                    </v-chip>
                                </v-flex>
                                <v-flex xs12 sm6>
                                    <v-container fluid>
                                        <v-switch :label="monday ? yes : no " v-model="monday"></v-switch>
                                    </v-container>
                                </v-flex>
                            </v-layout>

                            <!--Terca-feira-->
                            <v-layout>
                                <v-flex xs12 sm6>
                                    <v-chip style="float: left" class="ml-3" :color="tuesday ? availableColor : notAvailableColor" text-color="white">
                                        <v-avatar>
                                            <v-icon>check_circle</v-icon>
                                        </v-avatar>
                                        Terca-feira
                                    </v-chip>
                                </v-flex>
                                <v-flex xs12 sm6>
                                    <v-container fluid>
                                        <v-switch :label="tuesday ? yes : no " v-model="tuesday"></v-switch>
                                    </v-container>
                                </v-flex>
                            </v-layout>

                            <!--Quarta-feira-->
                            <v-layout>
                                <v-flex xs12 sm6>
                                    <v-chip style="float: left" class="ml-3" :color="wednesday ? availableColor : notAvailableColor" text-color="white">
                                        <v-avatar>
                                            <v-icon>check_circle</v-icon>
                                        </v-avatar>
                                        Quarta-feira
                                    </v-chip>
                                </v-flex>
                                <v-flex xs12 sm6>
                                    <v-container fluid>
                                        <v-switch :label="wednesday ? yes : no " v-model="wednesday"></v-switch>
                                    </v-container>
                                </v-flex>
                            </v-layout>

                            <!--Quinta-feira-->
                            <v-layout>
                                <v-flex xs12 sm6>
                                    <v-chip style="float: left" class="ml-3" :color="thursday ? availableColor : notAvailableColor" text-color="white">
                                        <v-avatar>
                                            <v-icon>check_circle</v-icon>
                                        </v-avatar>
                                        Quinta-feira
                                    </v-chip>
                                </v-flex>
                                <v-flex xs12 sm6>
                                    <v-container fluid>
                                        <v-switch :label="thursday ? yes : no " v-model="thursday"></v-switch>
                                    </v-container>
                                </v-flex>
                            </v-layout>

                            <!--Sexta-feira-->
                            <v-layout>
                                <v-flex xs12 sm6>
                                    <v-chip style="float: left" class="ml-3" :color="friday ? availableColor : notAvailableColor" text-color="white">
                                        <v-avatar>
                                            <v-icon>check_circle</v-icon>
                                        </v-avatar>
                                        Sexta-feira
                                    </v-chip>
                                </v-flex>
                                <v-flex xs12 sm6>
                                    <v-container fluid>
                                        <v-switch :label="friday ? yes : no " v-model="friday"></v-switch>
                                    </v-container>
                                </v-flex>
                            </v-layout>
                        </template>

                        <!--Fim de semana-->
                        <template>
                            <v-subheader>Fim de semana</v-subheader>

                            <!--Sabado-->
                            <v-layout>
                                <v-flex xs12 sm6>
                                    <v-chip style="float: left" class="ml-3" :color="saturday ? availableColor : notAvailableColor" text-color="white">
                                        <v-avatar>
                                            <v-icon>check_circle</v-icon>
                                        </v-avatar>
                                        Sabado
                                    </v-chip>
                                </v-flex>
                                <v-flex xs12 sm6>
                                    <v-container fluid>
                                        <v-switch :label="saturday ? yes : no " v-model="saturday"></v-switch>
                                    </v-container>
                                </v-flex>
                            </v-layout>

                            <!--Domingo-->
                            <v-layout>
                                <v-flex xs12 sm6>
                                    <v-chip style="float: left" class="ml-3" :color="sunday ? availableColor : notAvailableColor" text-color="white">
                                        <v-avatar>
                                            <v-icon>check_circle</v-icon>
                                        </v-avatar>
                                        Domingo
                                    </v-chip>
                                </v-flex>
                                <v-flex xs12 sm6>
                                    <v-container fluid>
                                        <v-switch :label="sunday ? yes : no " v-model="sunday"></v-switch>
                                    </v-container>
                                </v-flex>
                            </v-layout>

                            <v-layout class="mb-4">
                                <small style="float: left" class="ml-3 text-xs-left">
                                    NOTA: O facto de marcar sim, como disponivel nesse dia, nao significa que esta disponivel todo dia.
                                </small>
                            </v-layout>
                        </template>

                    </template>

                </v-list>
            </v-card>

        </v-flex>

    </div>

</template>

<script>
    export default {
        props: ['user', 'circularSize', 'showSubjectsEditDialog'],
        data: () => ({
            switch1: false,
            available: false,
            availableColor: 'blue',
            notAvailableColor: 'grey',
            monday: true,
            tuesday: true,
            wednesday: true,
            thursday: true,
            friday: true,
            saturday: true,
            sunday: true,
//            no: 'Não',
            no: 'Indisponível',
            yes: 'Sim',
            hidden: false,
            nameU: '',
            toggle: true,
        }),
        created () {
        },
        computed: {
            options () { //usado para fazer o scroll
                return {
                    duration: 1000,
                    offset: 0,
                    easing: 'easeInQuint'
                }
            },
            userAuth () {
                return this.$store.getters.userAuth
            },

            isUserAuth () {
                return this.userAuth !== null && this.userAuth !== undefined
            },

            isUser () {
                return this.user !== null && this.user !== undefined
            },
            isMyProfile () {
                return this.isUserAuth && this.isUser && this.user.id === this.userAuth.id
            },
            explainLevels ()
            {
                return this.isExplicador ? this.user.Explicador.explainLevels : ''
            },
            img () {
                return this.isUser ? this.user.photoURL : '//ssl.gstatic.com/s2/oz/images/sge/grey_silhouette.png'
            },
            nomeUser() {
                return this.isUser && this.user.name !== null && this.user.name !== '' ? this.user.name : 'Sem Nome'
            },

            isExplicador ()
            {
                if (!this.isUser) return false
                return this.user.Explicador && this.user.Explicador.explainLevels
            },

            explainClassesDetails () {

                if (!this.isExplicador) return false

                return this.user.Explicador.subjectDetails
            },
            userFaculty () {

                const negative = 'Sem Faculdade'

                if (!this.isUser) return negative

                let fac = this.user.Educations.faculty
                return this.isUser && fac !== null && fac !== undefined && fac !== '' ? 'Faculdade de ' + fac : negative
            },
            userCourse () {

                const negative = 'Sem Curso'

                if (!this.isUser) return negative

                let course = this.user.Educations.course

                return course && course !== null && course !== undefined && course !== '' ? 'Curso de ' + course : negative

            },
            userDepartment () {

                const negative = 'Sem Departamento'

                if (!this.isUser) return negative

                let department = this.user.Educations.department

                return department && department !== null && department !== undefined && department !== '' ? department : negative


            },
            userNivel () {

                const negative = 'Sem Nivel'

                if (!this.isUser) return negative

                let nivel = this.user.Educations.nivel

                return nivel && nivel !== null && nivel !== undefined && nivel !== '' ? nivel : negative


            },
            hasSubjects () {

                return this.isExplicador && this.user.Explicador.explainLevels
            },
            personalInfoFillPercent ()
            {
                return this.hasSubjects ? 100 : this.isExplicador ? 20 : 5;
            },

            subjectsTitle () {

                if (!this.hasSubjects && !this.isMyProfile) return 'Ainda não registou as disciplinas que explica'
                if (!this.hasSubjects && this.isMyProfile) return 'Não explicas nenhuma discipplina ?'

                let username = this.nomeUser.length > 18 ? this.nomeUser.slice(0,19) + '...'  : this.nomeUser

                return this.isMyProfile ? 'Minhas disciplinas' : 'Disciplinas do ' + username
            },
            userSubjects () {

                const negative = []

                if (!this.isUser) return negative

                let subjects = this.user.Educations.subjects

                return subjects


            },

            hasSomeLocation () {

                if ( !this.isUser ) return false

                return this.user.Location

            },

            userProvince () {

                const negative = 'Provincia Não registada'

                if (!this.hasSomeLocation) return negative

                let userProv = this.user.Location.province

                return userProv !== null && userProv !== undefined && userProv !== '' ? userProv : negative

            },

            userCity () {

                const negative = 'Cidade Não registada'

                if (!this.hasSomeLocation) return negative

                let userCity = this.user.Location.city

                return userCity !== null && userCity !== undefined && userCity !== '' ? userCity : negative

            },

            userBairro () {

                const negative = 'Bairro Não registado'

                if (!this.hasSomeLocation) return negative

                let userBairro = this.user.Location.bairro

                return userBairro !== null && userBairro !== undefined && userBairro !== '' ? userBairro : negative

            },

        },
        methods: {

            scrollTo (top) {

                window.scroll({
                    top: top,
                    left: 10,
                    behavior: 'smooth'
                });

                // Scroll to a certain element
//                document.querySelector(el).scrollIntoView({
//                    behavior: 'smooth'
//                });

            },

        },
    }
</script>
<style scoped>
    div.card__title {
        margin-top: 0px;
        padding: 0;
        font-size: 8pt;
    }
    .list__tile__title {
        font-size: 11px!important;
    }
    .headline {
        margin-left: 1em;
    }
    .list__tile__title {
        font-size: 12px!important;
    }
    .explicador-bar-fixed {
        z-index: 0;
    }
    .explicador-edit-item {
        float: right;
        cursor: pointer
    }
    .icon {
        font-size: 18px;
    }
    .img-radius {
        width: 50px;
        height: 50px!important;
    }
    .container {
        padding: 0px;
    }


</style>


